
"use client"

import { useState } from "react"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { MoreHorizontal, LayoutTemplate, Check } from "lucide-react"
import { TaskDetailSheet } from "@/components/work/task-detail-sheet"
import { AddTaskDialog } from "@/components/work/add-task-dialog"
import { User } from "@prisma/client"

interface ProjectTaskListProps {
    tasks: any[]
    users: User[]
    project: any
}

export function ProjectTaskList({ tasks, users, project }: ProjectTaskListProps) {
    const [selectedTask, setSelectedTask] = useState<any>(null)
    const [isSheetOpen, setIsSheetOpen] = useState(false)

    // Helper to refresh data if needed, but for now we rely on server actions revalidating path
    // If we wanted real-time or client-side filter updates, we'd need more logic here.

    const handleTaskClick = (task: any) => {
        setSelectedTask(task)
        setIsSheetOpen(true)
    }

    if (tasks.length === 0) {
        return (
            <div className="flex flex-col items-center justify-center p-8 border rounded-lg bg-muted/10 border-dashed text-center">
                <LayoutTemplate className="h-10 w-10 text-muted-foreground mb-4 opacity-50" />
                <h3 className="font-medium text-lg">No tasks yet</h3>
                <p className="text-sm text-muted-foreground max-w-sm mt-1 mb-4">
                    Get started by creating a new task or applying a template to this project.
                </p>
                <AddTaskDialog
                    projects={[{
                        id: project.id,
                        name: project.name,
                        allowedTaskTypes: project.allowedTaskTypes
                    }]}
                    defaultProjectId={project.id}
                />
            </div>
        )
    }

    return (
        <>
            <div className="border rounded-md overflow-hidden bg-card">
                {tasks.map((task: any) => (
                    <div
                        key={task.id}
                        onClick={() => handleTaskClick(task)}
                        className="group flex items-center justify-between p-4 border-b last:border-0 hover:bg-muted/50 transition-colors cursor-pointer"
                    >
                        <div className="flex items-start gap-3">
                            <div className="mt-1 flex items-center gap-2">
                                <Button
                                    variant="ghost"
                                    size="icon"
                                    className={`h-5 w-5 rounded-full border ${task.status === "DONE" ? "bg-primary text-primary-foreground border-primary" : "border-muted-foreground/30 hover:border-primary"}`}
                                    onClick={(e) => {
                                        e.stopPropagation()
                                        // If already done, toggle back to OPEN? Or just leave it?
                                        // User wants to "close" tasks.
                                        if (task.status !== "DONE") {
                                            setSelectedTask(task)
                                            // Trigger time log flow via sheet logic or directly?
                                            // The sheet has the time log dialog built-in.
                                            // Better to open sheet and trigger status change?
                                            // Or duplicate logic here?
                                            // Let's duplicate logic for cleaner UX (sheet doesn't open fully, just dialog)
                                            // But for now, let's open the sheet and maybe auto-trigger the status change?
                                            // Actually, easier:
                                            handleTaskClick(task)
                                            // We can't easily auto-trigger the dialog inside the sheet from here without complex state.
                                            // So for now, just clicking opens the sheet.
                                            // BUT user asked for "better way".
                                            // Let's make this button open a dedicated "Complete Task" dialog? 
                                            // Or reuse the TimeLogDialog directly here.
                                        }
                                    }}
                                >
                                    {task.status === "DONE" && <Check className="h-3 w-3" />}
                                </Button>
                                <Badge variant={task.status === "DONE" ? "outline" : "secondary"}>
                                    {task.status}
                                </Badge>
                            </div>
                            <div className="space-y-1">
                                <p className="font-medium leading-none">{task.title}</p>
                                <div className="flex items-center gap-2 text-xs text-muted-foreground">
                                    <span>{task.priority}</span>
                                    {task.taskType && (
                                        <>
                                            <span>•</span>
                                            <span>{task.taskType}</span>
                                        </>
                                    )}
                                    {task.dueDate && (
                                        <>
                                            <span>•</span>
                                            <span>Due {new Date(task.dueDate).toLocaleDateString()}</span>
                                        </>
                                    )}
                                </div>
                            </div>
                        </div>

                        <div className="flex items-center gap-4">
                            {task.assignee && (
                                <Avatar className="h-6 w-6">
                                    <AvatarImage src={task.assignee.avatarUrl} />
                                    <AvatarFallback className="text-[10px]">{task.assignee.name?.[0]}</AvatarFallback>
                                </Avatar>
                            )}
                            <Button variant="ghost" size="icon" className="h-8 w-8 opacity-0 group-hover:opacity-100">
                                <MoreHorizontal className="h-4 w-4" />
                            </Button>
                        </div>
                    </div>
                ))}
            </div>

            <TaskDetailSheet
                task={selectedTask}
                open={isSheetOpen}
                onOpenChange={setIsSheetOpen}
                users={users}
            />
        </>
    )
}
